import React from "react"
import "../styles/bootstrap.min.css"
import Layout from "../components/layout"
import { graphql } from "gatsby"

import Head from "../components/Head"
import Background from "../images/backgroundblog.jpg"

export const query = graphql`
  query {
    allContentfulPublications {
      edges {
        node {
          title
          description
          file {
            file {
              url
              fileName
            }
          }
          image {
            file {
              url
            }
          }
        }
      }
    }
  }
`
const publication = props => {
  const data = props.data.allContentfulPublications
  return (
    <Layout>
      <Head title="Publications" />

      <section
        className="page-title"
        style={{ backgroundImage: `url(${Background})` }}
      >
        <div className="auto-container">
          <div className="text-center">
            <h1>Publications</h1>
          </div>
        </div>
      </section>

      <section className="team-section">
        <div className="container">
          <div className="row clearfix">
            {/* <!--Column--> */}
            {data.edges.map(edge => {
              return (
                <div
                  className="col-md-6 col-sm-6 col-xs-12"
                  key={edge.node.title}
                >
                  <article className="team-area">
                    <div className="row clearfix">
                      <figure className="col-lg-6 col-md-12 image ">
                        <img
                          src={edge.node.image.file.url}
                          alt={edge.node.image.title}
                        />
                      </figure>
                      <div className="col-lg-6 col-md-12">
                        <div className="content pb-4 pl-2">
                          <div className="title-box">
                            <h4>{edge.node.title}</h4>
                          </div>
                          <div className="text">
                            <p>{edge.node.description}</p>
                          </div>
                          <p>{edge.node.publishedDate}</p>
                          <a
                            href={`${edge.node.file.file.url}`}
                            style={{
                              boxShadow: `none`,
                              textDecoration: `none`,
                              color: `inherit`,
                            }}
                            className="btn-thm btn-xs"
                            target="_blank"
                            download
                            rel="noopener noreferrer"
                          >
                            Download
                          </a>
                        </div>
                        <div></div>
                      </div>
                    </div>
                  </article>
                </div>
              )
            })}
          </div>
        </div>
      </section>
    </Layout>
  )
}
export default publication
